export const customStyles = {
  control: (base, state) => ({
    ...base,
    borderColor: state.isFocused ? "#383332" : "##383332",
    boxShadow: state.isFocused ? null : null,
    "&:hover": { borderColor: state.isFocused ? null : null },
    minHeight: 28,
    height: 28,
    width: "14rem",
    padding: "0px 0px",
  }),
  option: (base, state) => ({
    ...base,
    backgroundColor: state.isSelected ? "#FFC467" : "##F8F4F0",
    color: state.isSelected ? "#383332" : "#383332",
    "&:hover": { backgroundColor: "#F8F4F0" },
    minHeight: 18,
    width: 250,
    paddingBottom: 4,
  }),
  dropdownIndicator: (base) => ({
    ...base,
    padding: 4,
  }),
  input: (base, state) => ({
    ...base,
    padding: "0px 0px 0px",
  }),
  loadingIndicator: (base) => ({
    ...base,
    display: "none",
  }),
  menuList: (base) => ({
    ...base,
    maxHeight: 200,

    "::-webkit-scrollbar": {
      width: "4px",
      height: "0px",
    },
    "::-webkit-scrollbar-track": {
      background: "#f1f1f1",
    },
    "::-webkit-scrollbar-thumb": {
      background: "#888",
    },
    "::-webkit-scrollbar-thumb:hover": {
      background: "#555",
    },
  }),
};

export const logout = () => {
  localStorage.removeItem("beaconToken");
  localStorage.removeItem("tenantToken");
  localStorage.removeItem("projectExchangeToken");
  localStorage.removeItem("expiry");
  localStorage.removeItem("tenantId");
  localStorage.clear();
};

export const showErrorMessage = (id, message, buttonId = null) => {
  buttonId ? (document.getElementById(buttonId).style.display = "none") : null;
  document.getElementById(id).style.display = "block";
  document.getElementById(id).innerHTML = message;
  setTimeout(() => {
    document.getElementById(id).style.display = "none";
    buttonId ? (document.getElementById(buttonId).style.display = "block") : null;
    document.getElementById(id).innerHTML = "";
  }, 3000);
};
export const successMessage = (id, message) => {
  document.getElementById(id).style.display = "block";
  document.getElementById(id).innerHTML = message;
  setTimeout(() => {
    document.getElementById(id).style.display = "none";
    document.getElementById(id).innerHTML = "";
  }, 3000);
};
export const checkAlreadyReserved = async (mappingConfig, selectedAddress) => {
  console.log(mappingConfig, "mappi config at check");
  return new Promise((resolve, reject) => {
    if (Object.keys(mappingConfig).length) {
      console.log("inide");
      Object.entries(mappingConfig)?.reduce((check, element) => {
        if (element[1]?.address === selectedAddress) resolve(true);
      });
      resolve(false);
    } else {
      console.log("false");
      resolve(false);
    }
  });
};
export const checkAlreadyReservedRange = async (mappingConfig, selectedAddress) => {
  return new Promise(async (resolve, reject) => {
    if (mappingConfig) {
      for (let element in mappingConfig) {
        for (let i = 0; i < mappingConfig[element]?.address?.length; i++) {
          if (await checkInterSectionOfRange(selectedAddress, mappingConfig[element]?.address[i])) resolve(true);
        }
      }

      resolve(false);
    } else {
      resolve(false);
    }
  });
};

const checkInterSectionOfRange = async (address1, address2) => {
  try {
    return await Excel.run(async function (context) {
      const range = context.workbook.worksheets
        .getActiveWorksheet()
        .getRange(address1)
        .getIntersectionOrNullObject(address2);
      range.load("address");
      await context.sync();
      if (range.address) return true;
    });
  } catch (error) {
    console.log(error);
  }
};
