// deletes all sheets in the workbook
export const deleteAllSheets = async () => {
  const sheetNames = await getAllsheetNames();
  return Excel.run(async function (context) {
    sheetNames.forEach(async function (sheet) {
      context.workbook.worksheets.getItem(sheet).delete();
      await context.sync();
    });
  }).catch(function (error) {
    console.log("Error: " + error);
  });
};
