export const hideColumn = async (columnName) => {
  await Excel.run(async function (context) {
    let sheet = context.workbook.worksheets.getActiveWorksheet();

    let range = sheet.getRange(columnName + ":" + columnName);
    range.format.columnWidth = 0; // Set the column width to zero to hide the columns

    // Run the command
    await context.sync();
  }).catch(function (error) {
    console.log(error);
  });
};

export const setCustomPropertyData = async (sheetId, data) => {
  sheetId = sheetId ?? (await getactiveSheetId());
  console.log(sheetId, "sheetId set", data);
  Excel.run(function (context) {
    let range = context.workbook.worksheets.getItem(sheetId);

    // Set the value of a custom document property
    for (let key in data) {
      range.customProperties.add(`${key}`, `${data[key]}`);
    }

    return context.sync().then(async function () {});
  }).catch(function (error) {
    console.log(error);
  });
};
export const getcustomProperyData = async (sheetId) => {
  try {
    return await Excel.run(async (context) => {
      // Load the keys and values of all custom properties in the current worksheet.
      const customWorksheetProperties = context.workbook.worksheets.getItem(sheetId).customProperties;
      customWorksheetProperties.load(["key", "value"]);
      await context.sync();
      let excelBackData = {};
      if (customWorksheetProperties.items.length > 0) {
        customWorksheetProperties?.items?.forEach((property) => {
          // console.log(`${property.key}:${property.value}`);
          excelBackData[property?.key] = property.value;
        });
        return excelBackData;
      } else {
        return false;
      }
    });
  } catch (error) {
    console.log(error);
  }
};
export const checkForAddressExistance = (mappingConfig, key) => {
  return new Promise((resolve, reject) => {
    Excel.run((context) => {
      let selectedRange = context.workbook.worksheets.getActiveWorksheet().getSelectedRange().load("address");
      context.sync().then(() => {
        let address = getAddressWithOutSheetName(selectedRange.address);
        for (let i = 0; i < mappingConfig[key].address?.length; i++) {
          if (address === mappingConfig[key].address[i]) resolve(true);
        }
        resolve(false);
      });
    });
  });
};
export const clearGivenRange = (address) => {
  console.log(address);
  Excel.run(async function (context) {
    context.workbook.worksheets.getActiveWorksheet().getRange(address).clear();
    var range = context.workbook.getSelectedRange();
    range.load();

    return context.sync().then(async function () {
      range.clear("Contents/format");

      await context.sync();
    });
  });
};
