import React, { useState, useEffect, Suspense } from "react";
import { useDispatch, useSelector } from "react-redux";
import NotFoundPage from "../pages/NotFoundPage/NotFoundPage";
import { setNextPage } from "../redux-toolkit/slice/TaskpaneSlice";
import { logout } from "../constFunctions/copmponentFunctions";
import { setActivesheetId } from "../redux-toolkit/slice/action";
import { getactiveSheetId } from "../constFunctions/excelFunction";
import { g } from "../../globalState/globalState";
const MM = React.lazy(() => import("../pages/MM/MM"));
const HomeLogin = React.lazy(() => import("../pages/HomeLogin/HomeLogin"));
const Forms = React.lazy(() => import("../pages/Forms/Forms"));
const Projects = React.lazy(() => import("../pages/Projects/Projects"));
const Bom = React.lazy(() => import("../pages/Bom/Bom"));
const Companys = React.lazy(() => import("../pages/Companys/Companys"));

function App() {
  const [taskPanePage, setTaskPanePage] = useState({ taskPanePage: null });
  const { nextPage } = useSelector((state) => state.taskpane);
  const dispatch = useDispatch();
  // this function works as event emitter while chnaging active sheet
  Office.onReady(function () {
    Excel.run(function (context) {
      var worksheets = context.workbook.worksheets;
      context.load(worksheets, "items");

      return context
        .sync()

        .then(function () {
          worksheets.onActivated.add(onWorksheetChanged);
        })
        .then(context.sync);
    }).catch(function (error) {
      console.log(error);
    });
  });

  function onWorksheetChanged(eventArgs) {
    g.sheetactivated = eventArgs.worksheetId;
    dispatch(setActivesheetId(eventArgs.worksheetId));
  }
  useEffect(() => {
    (async () => {
      let sheetId = await getactiveSheetId();
      dispatch(setActivesheetId(sheetId));
    })();
  }, [true]);
  useEffect(() => {
    setTaskPanePage(localStorage.getItem("taskPanePage"));
  }, [nextPage]);
  useEffect(() => {
    let page = localStorage.getItem("taskPanePage");
    if (page !== taskPanePage) {
      dispatch(setNextPage(page));
    }
  });
  useEffect(() => {
    if (localStorage.getItem("beaconToken")) {
    } else dispatch(setNextPage("homeLogin"));
  }, [nextPage]);
  useEffect(() => {
    if (new Date() > new Date(localStorage.getItem("expiry") * 1000)) {
      logout();
      dispatch(setNextPage("homeLogin"));
    }
  }, [true]);
  let obj = {
    // configureForm: <MM/>,
    homeLogin: (
      <Suspense fallback={<div></div>}>
        <HomeLogin />
      </Suspense>
    ),
    material: (
      <Suspense fallback={<div></div>}>
        <MM />
      </Suspense>
    ),
    notFoundPage: (
      <Suspense fallback={<div></div>}>
        <NotFoundPage />
      </Suspense>
    ),
    forms: (
      <Suspense fallback={<div></div>}>
        {" "}
        <Forms />
      </Suspense>
    ),
    projects: (
      <Suspense fallback={<div></div>}>
        <Projects />
      </Suspense>
    ),
    bom: (
      <Suspense fallback={<div></div>}>
        <Bom />
      </Suspense>
    ),
    companys: (
      <Suspense fallback={<div></div>}>
        <Companys />
      </Suspense>
    ),
  };

  if (obj[taskPanePage]) {
    return obj[taskPanePage];
  } else {
    return obj["notFoundPage"];
  }
}
export default App;
