import React from "react";
import "./SideBarMenu.css";
import { useDispatch, useSelector } from "react-redux";
import { setNextPage } from "../../redux-toolkit/slice/TaskpaneSlice";
import localStorage from "redux-persist/es/storage";
import { setSideBarOpen } from "../../redux-toolkit/slice/action";
import ProjectLogo from "../../../../assets/icons/projects-icon (2).svg";
import MaterialLogo from "../../../../assets/icons/material.svg";
import FormsLogo from "../../../../assets/icons/forms.svg";
import BomLogo from "../../../../assets/icons/bom.svg";
import LogoutLogo from "../../../../assets/icons/logout.svg";
import CloseLogo from "../../../../assets/icons/x-circle.svg";
import MenuLogo from "../../../../assets/icons/menu.svg";
import Companys from "../../../../assets/icons/companys.svg";

function SideBarMenu() {
  const { nextPage } = useSelector((state) => state?.taskpane);
  const { sideBarOpen } = useSelector((state) => state?.action);
  const dispatch = useDispatch();
  const handleTrigger = () => dispatch(setSideBarOpen(!sideBarOpen));
  const handleLogout = () => {
    localStorage.removeItem("beaconToken");
    localStorage.removeItem("tenantToken");
    localStorage.removeItem("projectExchangeToken");
    dispatch(setNextPage("homeLogin"));
  };
  return (
    <>
      <div className={`sidebar ${sideBarOpen ? "sidebar--open" : ""}`}>
        <div className="trigger" onClick={handleTrigger}>
          {sideBarOpen ? (
            <img className="sidebar-logo-img" src={CloseLogo} />
          ) : (
            <img className="sidebar-logo-img" src={MenuLogo} />
          )}
        </div>

        <div
          className={`${nextPage === "material" ? "selected-menu" : null} sidebar-position`}
          onClick={() => {
            dispatch(setNextPage("material"));
            dispatch(setSideBarOpen(false));
          }}
        >
          <img className="sidebar-logo-img" src={MaterialLogo} />
          <span>Material</span>
        </div>

        <div
          className={`${nextPage === "bom" ? "selected-menu" : null} sidebar-position`}
          onClick={() => {
            dispatch(setNextPage("bom"));
            dispatch(setSideBarOpen(false));
          }}
        >
          <img className="sidebar-logo-img" src={BomLogo} />
          <span> BOM</span>
        </div>

        <div
          className={`${nextPage === "projects" ? "selected-menu" : null} sidebar-position`}
          onClick={() => {
            dispatch(setNextPage("projects"));
            dispatch(setSideBarOpen(false));
          }}
        >
          <img className="sidebar-logo-img" src={ProjectLogo} />

          <span>Projects</span>
        </div>
        <div
          className={`${nextPage === "companys" ? "selected-menu" : null} sidebar-position`}
          onClick={() => {
            dispatch(setNextPage("companys"));
            dispatch(setSideBarOpen(false));
          }}
        >
          <img className="sidebar-logo-img" src={Companys} />
          <span>Companies</span>
        </div>
        <div
          className={`${nextPage === "forms" ? "selected-menu" : null} sidebar-position`}
          onClick={() => {
            dispatch(setNextPage("forms"));
            dispatch(setSideBarOpen(false));
          }}
        >
          <img className="sidebar-logo-img" src={FormsLogo} />
          <span>forms</span>
        </div>

        <div
          className={`sidebar-position`}
          onClick={() => {
            dispatch(setSideBarOpen(false));
            handleLogout();
          }}
        >
          <img className="sidebar-logo-img" src={LogoutLogo} />
          <span>Logout</span>
        </div>
      </div>
    </>
  );
}

export default SideBarMenu;
