import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  materialAction: "get",
  sideBarOpen: false,
  lastUsedColumn: null,
  bomAction: "map",
  uploadLoader: false, // to set loader while exporting data
  singleMaterialData: null,
  bomName: "",
  activeSheetId: null,
  coreMaterialDataAtBomUpdate: null,
  formAction: "map",
  MMmappingDataTemp: {}, // here the mapping will store while editing
  bomMappingDataTemp: {
    id: { name: "ID", address: "A" },
  },
};

export const actionSlice = createSlice({
  name: "actionSlice",
  initialState,
  reducers: {
    setMaterialAction: (state, action) => {
      state.materialAction = action.payload;
    },
    setSideBarOpen: (state, action) => {
      state.sideBarOpen = action.payload;
    },
    setLastUsedColumn: (state, action) => {
      state.lastUsedColumn = action.payload;
    },
    setBomAction: (state, action) => {
      state.bomAction = action.payload;
    },
    setUploadLoader: (state, action) => {
      state.uploadLoader = action.payload;
      if (action.payload) document.getElementById("commonContainer").style.display = "none";
      else document.getElementById("commonContainer").style.display = "block";
    },
    setSingleMaterialData: (state, action) => {
      if (action.payload)
        state.singleMaterialData = { ...state.singleMaterialData, associated_product_code_bom: action.payload };
      else state.singleMaterialData = null;
    },
    setSingleMaterialDataInitial: (state, action) => {
      state.singleMaterialData = action.payload;
    },
    setBomName: (state, action) => {
      state.bomName = action.payload;
    },
    setActivesheetId: (state, action) => {
      state.activeSheetId = action.payload;
    },
    setCoreMaterialDataAtBomUpdate: (state, action) => {
      state.coreMaterialDataAtBomUpdate = action.payload;
    },
    setFormAction: (state, action) => {
      state.formAction = action.payload;
    },
    setMMmappingTempData: (state, action) => {
      state.MMmappingDataTemp = {
        ...state.MMmappingDataTemp,
        [action.payload.value]: {
          name: action.payload.name,
          address: action.payload.address,
          columnNumber: action.payload.columnNumber,
        },
      };
    },
    setMMmappingTempData2: (state, action) => {
      state.MMmappingDataTemp = action.payload;
    },
    setBomMappingTempData: (state, action) => {
      state.bomMappingDataTemp = {
        ...state.bomMappingDataTemp,
        [action.payload.value]: {
          name: action.payload.name,
          address: action.payload.address,
          columnNumber: action.payload.columnNumber,
        },
      };
    },
    setBomMappingTempData2: (state, action) => {
      state.bomMappingDataTemp = action.payload;
    },
  },
});

export const {
  setMaterialAction,
  setSideBarOpen,
  setLastUsedColumn,
  setBomAction,
  setUploadLoader,
  setSingleMaterialData,
  setSingleMaterialDataInitial,
  setBomName,
  setActivesheetId,
  setCoreMaterialDataAtBomUpdate,
  setFormAction,
  setMMmappingTempData,
  setMMmappingTempData2,
  setBomMappingTempData,
  setBomMappingTempData2,
} = actionSlice.actions;

export default actionSlice.reducer;
